var Decimal = require('decimal.js');

var metadata = require('./data/current/metadata.json');

var table1Single = require('./data/current/table1-single.json');

var table2PartnerOAS = require('./data/current/table2-partner-oas.json');

var table3PartnerNoOAS = require('./data/current/table3-partner-no-oas.json');

var STATUS_ENUM = Object.freeze({
  SINGLE: 'SINGLE',
  PARTNER_OAS: 'PARTNER_OAS',
  PARTNER_NO_OAS: 'PARTNER_NO_OAS'
});

function _validate(status, income) {
  var dIncome;
  var dIncomeNeg;
  var result = {
    isValid: true,
    messages: []
  };

  if (!STATUS_ENUM[status]) {
    result.messages.push({
      en_CA: 'Unknown status',
      fr_CA: 'TBD'
    });
    result.isValid = false;
  }

  try {
    dIncome = new Decimal(income);
    dIncomeNeg = dIncome.isNeg();
  } catch (err) {
    result.messages.push({
      en_CA: 'Invalid income',
      fr_CA: 'TBD'
    });
    result.isValid = false;
  }

  if (dIncomeNeg) {
    result.messages.push({
      en_CA: 'Income cannot be negative',
      fr_CA: 'TBD'
    });
    result.isValid = false;
  }

  return result;
}

function _lookupTable(status) {
  if (status === STATUS_ENUM.SINGLE) {
    return table1Single;
  }

  if (status === STATUS_ENUM.PARTNER_OAS) {
    return table2PartnerOAS;
  }

  if (status === STATUS_ENUM.PARTNER_NO_OAS) {
    return table3PartnerNoOAS;
  }
}

function splitDate(val) {
  var parts = val.split(' ');
  return parts[0];
}

function parseCoveragePeriod(metadata) {
  var start = splitDate(metadata.result.time_period_coverage_start);
  var end = splitDate(metadata.result.time_period_coverage_end);
  return {
    start: start,
    end: end
  };
}

function find(status, income) {
  var validateResult = _validate(status, income);

  if (!validateResult.isValid) {
    return {
      input: {
        status: status,
        income: income
      },
      error: validateResult.messages
    };
  }

  var lookupTable = _lookupTable(status);

  var result = lookupTable.find(function (gisData) {
    var dFrom = new Decimal(gisData.from);
    var dTo = new Decimal(gisData.to);
    var dIncome = new Decimal(income);
    return dFrom.lte(dIncome) && dTo.gte(dIncome);
  });
  return {
    input: {
      status: status,
      income: income
    },
    output: result ? result : {
      gis: '0'
    },
    metadata: metadata,
    coverage: parseCoveragePeriod(metadata)
  };
}

module.exports = {
  find: find,
  STATUS: STATUS_ENUM
};